.menu > .nav-item {
  position: relative;
  padding-bottom: 35px;
  padding-top: 35px;
}

.menu > .nav-item > .nav-link {
  font: 500 15px "Poppins", sans-serif;
  color: #051441;
  padding: 0px;
  transition: color 0.3s ease 0s;
  position: relative;
  cursor: pointer;
}

.menu > .nav-item > .nav-link:before {
  content: "";
  width: 0;
  height: 1px;
  background: #3ba266;
  position: absolute;
  bottom: 0;
  left: auto;
  right: 0;
  transition: all 0.2s linear;
}

.menu > .nav-item > .nav-link:after {
  display: none;
}

.menu > .nav-item.submenu .dropdown-menu {
  margin: 0px;
  border: 0px;
  margin: 0px;
  border-radius: 0px;
  left: -40px;
  min-width: 200px;
  padding: 0px;
  background: #fff;
