/*========= hamburger menu css ========*/
.navbar-toggler {
  padding: 0px;
  margin-left: 20px;
}

.navbar-toggler:focus {
  outline: none;
}

.menu_toggle {
  width: 22px;
  height: 22px;
  position: relative;
  cursor: pointer;
  display: block;
}

.menu_toggle .hamburger {
  position: absolute;
  height: 100%;
  width: 100%;
  display: block;
}

.menu_toggle .hamburger span {
  width: 0%;
  height: 2px;
  position: relative;
  top: 0;
  left: 0;
  margin: 4px 0;
  display: block;
  background: #3ba266;
  border-radius: 3px;
  transition: 0.2s ease-in-out;
}

.menu_toggle .hamburger span:nth-child(1) {
  transition-delay: 0s;
}

.menu_toggle .hamburger span:nth-child(2) {
  transition-delay: 0.125s;
}

.menu_toggle .hamburger span:nth-child(3) {
  transition-delay: 0.2s;
}

.menu_toggle .hamburger-cross {
  position: absolute;
  height: 100%;
  width: 100%;
  -webkit-transform: rotate(45deg);
