.download_parallax_bg {
  position: absolute;
  width: 100%;
  height: 100%;
  background: url(data:image/png;base64,) no-repeat scroll center center;
  z-index: -1;
}

.download_content .download_btn {
  width: 250px;
  height: 250px;
  border-radius: 50%;
  border: 1px solid #f6f5fc;
  display: inline-block;
  text-align: center;
  position: relative;
  z-index: 0;
  margin-bottom: 60px;
}

.download_content .download_btn:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 200px;
  height: 200px;
  background: #f7f4ff;
  border-radius: 50%;
  z-index: -2;
}

.download_content .download_btn:after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 160px;
  height: 160px;
  background: #f1ecff;
  border-radius: 50%;
  z-index: -1;
}

.download_content .download_btn .pulse-x {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  background: #f1ecff;
  width: 110px;
  height: 110px;
  border-radius: 100%;
  z-index: -2;
  -webkit-animation: pulsate 3s infinite;
  animation: pulsate 3s infinite;
}

.download_content .download_btn i {
  width: 120px;
  height: 120px;
  display: inline-block;
  background-image: -ms-linear-gradient(50deg, #3ba266 0%, #3ba266 100%);
  box-shadow: inset 2px -3.464px 6px 0px rgba(34, 10, 104, 0.2);
  border-radius: 50%;
  line-height: 120px;
  font-size: 55px;
  color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

