  font: 500 14px "Poppins", sans-serif;
  color: #23b1fe;
  display: inline-block;
  padding: 15px 31px;
  position: relative;
  min-width: 140px;
  border-radius: 4px;
  background-image: -moz-linear-gradient(
    -48deg,
    #237efd 0%,
    #24b0fe 46%,
    #24e2ff 100%
  );
  background-image: -webkit-linear-gradient(
    -48deg,
    #237efd 0%,
    #24b0fe 46%,
    #24e2ff 100%
  );
  background-image: -ms-linear-gradient(
    -48deg,
    #237efd 0%,
    #24b0fe 46%,
